
'use client';

import type { ReactNode } from 'react';
import { redirect, usePathname } from 'next/navigation';
import { useEffect, useState } from 'react';
import type { User as CurrentUserType } from '@/types';
import { fetchUserSessionForClient } from '@/app/actions';
import { Loader2 as LoaderIcon } from 'lucide-react';

export default function DashboardLayout({
  children,
}: {
  children: ReactNode;
}) {
  const [user, setUser] = useState<CurrentUserType | null>(null);
  const [loading, setLoading] = useState(true);
  const pathname = usePathname();

  useEffect(() => {
    async function fetchSession() {
      setLoading(true);
      try {
        const sessionUser = await fetchUserSessionForClient();
        // Allow all logged-in users to access the dashboard.
        // Specific admin functionalities are handled by the /admin route.
        if (!sessionUser) { 
          redirect(`/login?message=请登录以访问仪表盘&redirect=${pathname}`);
        } else {
          setUser(sessionUser);
        }
      } catch (error) {
        console.error("Error fetching user session in dashboard layout:", error);
        redirect(`/login?message=会话加载失败，请重新登录&redirect=${pathname}`);
      } finally {
        setLoading(false);
      }
    }
    fetchSession();
  }, [pathname]);

  if (loading) {
    return (
      <div className="flex flex-1 justify-center items-center h-full p-4 bg-background">
        <LoaderIcon className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-3 text-lg text-muted-foreground">正在加载用户数据...</p>
      </div>
    );
  }

  if (!user) {
    // This case should ideally be handled by the redirect in useEffect,
    // but as a fallback, show a message.
    return (
        <div className="flex flex-1 justify-center items-center h-full p-4 bg-background">
            <p className="text-muted-foreground">正在重定向到登录页面...</p>
        </div>
    );
  }

  return <>{children}</>;
}

